body{margin:0;background:#3fbdbd;font-family:"Montserrat",sans-serif;font-size:x-large;text-align:justify}#root{height:100vh}
.ProjectContainer{display:grid;grid-template-columns:1fr;grid-column-gap:2vw;-webkit-column-gap:2vw;-moz-column-gap:2vw;column-gap:2vw;grid-row-gap:3vh;row-gap:3vh;padding:2%;padding-bottom:4%;justify-content:center}@media only screen and (min-width: 600px){.ProjectContainer{grid-template-columns:1fr 1fr}}@media only screen and (min-width: 1000px){.ProjectContainer{grid-template-columns:1fr 1fr 1fr}}
.Header{box-shadow:0 4px 8px 0 rgba(0,0,0,.2),0 6px 20px 0 rgba(0,0,0,.19);background-color:#d72121;display:flex;text-decoration:none;font-family:"Raleway",sans-serif;height:40px;font-size:18px}@media only screen and (min-width: 350px){.Header{font-size:calc((11vw - 15px) * .75)}}@media only screen and (min-width: 510px){.Header{font-size:calc(40px * 0.75)}}.Header ul{margin:0;padding-left:0;width:100%;display:flex;flex-direction:row;align-items:center;justify-content:space-around;list-style-type:none;height:100%}.Header ul li a{text-decoration:none;padding:3px 5px 2px 5px;color:#fff;-webkit-transition:background-color .5s cubic-bezier(0.1, 0.8, 0.9, 0.9);transition:background-color .5s cubic-bezier(0.1, 0.8, 0.9, 0.9)}.Header ul li a:hover{background-color:#2e8888;cursor:pointer}
.ImageCard{padding:3%;display:flex;align-items:center;background:#2e8888;border-radius:12px;-webkit-transition:box-shadow .5s,-webkit-transform .5s ease-in-out;transition:box-shadow .5s,-webkit-transform .5s ease-in-out;transition:box-shadow .5s,transform .5s ease-in-out;transition:box-shadow .5s,transform .5s ease-in-out,-webkit-transform .5s ease-in-out;position:relative}.ImageCard .image{max-height:100%;max-width:100%;object-fit:contain}.ImageCard .text{display:flex;align-items:flex-end;justify-content:flex-end;width:calc(100% - 6% - 20px);height:calc(100% - 9% - 20px);position:absolute;background:#1d6a9a4d;padding:10px;text-align:right;font-size:medium;color:#fff;visibility:hidden;opacity:0;overflow:hidden;-webkit-transition:opacity .5s,visibility .2s;transition:opacity .5s,visibility .2s}.Clickable{box-shadow:0 2px 4px 0 rgba(0,0,0,.2),0 3px 10px 0 rgba(0,0,0,.19)}.Clickable:hover{box-shadow:0 8px 12px 0 rgba(0,0,0,.2),0 6px 20px 0 rgba(0,0,0,.19);cursor:pointer}.ImageCard:hover .text{visibility:visible;opacity:1}
.body{font-family:"Montserrat",sans-serif;font-size:calc(1rem + 0.3vw);text-align:justify;text-indent:2em}.heading{font-size:18px;font-family:"Raleway",sans-serif;text-align:center}@media only screen and (min-width: 350px){.heading{font-size:calc((11vw - 15px) * .75)}}@media only screen and (min-width: 510px){.heading{font-size:calc(40px * 0.75)}}
.IconButtons{padding:.5vw;border-radius:12px;background-color:#d72121;display:grid;grid-template-columns:auto auto auto;grid-gap:15%;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;min-width:9em;max-height:2em;align-items:center;justify-content:center}.IconButtons .icon:hover{-webkit-filter:drop-shadow(0 0 16px rgba(0, 0, 0, 0.4));filter:drop-shadow(0 0 16px rgba(0, 0, 0, 0.4));cursor:pointer}
.Layout{min-width:350px;scroll-behavior:smooth;height:100%}.Layout .header_container{position:-webkit-sticky;position:sticky;top:0;width:100%;z-index:99}.Layout .Body{height:calc(100% - 40px)}
.Layout body{display:flex;flex-direction:column;align-items:center}.Layout body h1{font-family:"Raleway",sans-serif}.Layout body p{font-family:"Montserrat",sans-serif}
.Contact{padding:2%;display:grid;grid-template-rows:auto auto auto;justify-items:center;align-items:center;height:calc(100% - 4% - 40px)}.Contact .card_container{width:80vw}@media only screen and (min-width: 400px){.Contact .card_container{width:25vw;min-width:320px}}.Contact .text_container{width:100%}@media only screen and (min-width: 545px){.Contact .text_container{width:80vw;min-width:545px}}.Contact .button_container{display:flex;width:100%;justify-content:center}
.About{margin:2%;display:flex;flex-flow:row wrap;align-items:center;justify-content:space-around}.About>*{margin:1%;min-width:330px;flex-basis:100vw}.About .profile_text .heading{overflow:hidden;height:0}.About .profile_picture{flex-basis:60vw}@media only screen and (min-width: 888px){.About .profile_picture{flex-basis:35vw}.About .profile_text{flex-basis:55vw}.About .profile_text .heading{height:auto}.About .project_picture{flex-basis:45vw;order:2}.About .project_text{flex-basis:45vw}}@media only screen and (min-width: 1000px){.About .end_picture{order:2}}
.Cube{margin:2%;display:flex;flex-flow:row wrap;align-items:center;justify-content:space-around;font-size:large;text-indent:2em}.Cube>*{margin:1%;min-width:330px;flex-basis:100vw}@media only screen and (min-width: 600px){.Cube .main_picture{flex-basis:35vw}.Cube .main_text{max-width:55vw}.Cube .end_picture{max-width:45vw}.Cube .end_text{max-width:45vw}}@media only screen and (min-width: 1000px){.Cube .end_picture{order:2}}@media only screen and (min-width: 1250px){.Cube{font-size:x-large}}
